import React, { useState, useEffect } from "react";
import useScrollReveal from "../hooks/useScrollReveal";

interface QuoteFormData {
  websiteType: string;
  hasBackend: boolean;
  hasCMS: boolean;
  hasEcommerce: boolean;
  hasEmailNewsletter: boolean;
  hasCustomDesign: boolean;
  hasResponsiveDesign: boolean;
  hasHosting: boolean;
  hasMaintenance: boolean;
  numberOfPages: string;
  timeline: string;
}

const websiteTypes = [
  { value: 'static', label: 'Static Website', basePrice: 500 },
  { value: 'dynamic', label: 'Dynamic Website', basePrice: 1500 },
  { value: 'ecommerce', label: 'E-commerce Website', basePrice: 3000 },
  { value: 'webapp', label: 'Web Application', basePrice: 5000 },
  { value: 'custom', label: 'Custom Solution', basePrice: 7500 }
];

const pageRanges = [
  { value: '1-3', label: '1-3 Pages', multiplier: 1 },
  { value: '4-10', label: '4-10 Pages', multiplier: 1.5 },
  { value: '11-25', label: '11-25 Pages', multiplier: 2 },
  { value: '26-50', label: '26-50 Pages', multiplier: 3 },
  { value: '50+', label: '50+ Pages', multiplier: 4 }
];

const timelines = [
  { value: '1-2weeks', label: '1-2 Weeks', multiplier: 1.5 },
  { value: '3-4weeks', label: '3-4 Weeks', multiplier: 1.2 },
  { value: '1-2months', label: '1-2 Months', multiplier: 1 },
  { value: '3+months', label: '3+ Months', multiplier: 0.9 }
];

const additionalFeatures = [
  { key: 'hasBackend', label: 'Backend Development', price: 2000 },
  { key: 'hasCMS', label: 'Content Management System', price: 1500 },
  { key: 'hasEcommerce', label: 'E-commerce Integration', price: 2500 },
  { key: 'hasEmailNewsletter', label: 'Email Newsletter Setup', price: 500 },
  { key: 'hasCustomDesign', label: 'Custom Design', price: 1000 },
  { key: 'hasResponsiveDesign', label: 'Responsive Design', price: 500 },
  { key: 'hasHosting', label: 'Hosting Setup', price: 200 },
  { key: 'hasMaintenance', label: 'Monthly Maintenance', price: 150 }
];

const GetQuote: React.FC = () => {
  useScrollReveal();
  
  const [formData, setFormData] = useState<QuoteFormData>({
    websiteType: '',
    hasBackend: false,
    hasCMS: false,
    hasEcommerce: false,
    hasEmailNewsletter: false,
    hasCustomDesign: false,
    hasResponsiveDesign: true, // Default to true as it's essential
    hasHosting: false,
    hasMaintenance: false,
    numberOfPages: '',
    timeline: ''
  });

  const [estimatedPrice, setEstimatedPrice] = useState(0);
  const [showQuote, setShowQuote] = useState(false);
  const [isCalculating, setIsCalculating] = useState(false);

  const calculatePrice = () => {
    if (!formData.websiteType || !formData.numberOfPages || !formData.timeline) {
      return 0;
    }

    const websiteType = websiteTypes.find(type => type.value === formData.websiteType);
    const pageRange = pageRanges.find(range => range.value === formData.numberOfPages);
    const timeline = timelines.find(t => t.value === formData.timeline);

    if (!websiteType || !pageRange || !timeline) return 0;

    let basePrice = websiteType.basePrice;
    
    // Apply page multiplier
    basePrice *= pageRange.multiplier;
    
    // Apply timeline multiplier
    basePrice *= timeline.multiplier;

    // Add additional features
    additionalFeatures.forEach(feature => {
      if (formData[feature.key as keyof QuoteFormData]) {
        basePrice += feature.price;
      }
    });

    return Math.round(basePrice);
  };

  useEffect(() => {
    setIsCalculating(true);
    const timer = setTimeout(() => {
      const price = calculatePrice();
      setEstimatedPrice(price);
      setShowQuote(price > 0);
      setIsCalculating(false);
    }, 300); // Small delay for better UX
    
    return () => clearTimeout(timer);
  }, [formData]);

  const handleInputChange = (field: keyof QuoteFormData, value: any) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleGetQuote = () => {
    // Create a mailto link with quote details
    const quoteDetails = {
      ...formData,
      estimatedPrice,
      timestamp: new Date().toISOString()
    };
    
    const emailSubject = `Quote Request - ${websiteTypes.find(t => t.value === formData.websiteType)?.label || 'Website Project'}`;
    const emailBody = `Hi Avexel Team,

I would like to request a detailed quote for my project with the following specifications:

Website Type: ${websiteTypes.find(t => t.value === formData.websiteType)?.label}
Number of Pages: ${pageRanges.find(p => p.value === formData.numberOfPages)?.label}
Timeline: ${timelines.find(t => t.value === formData.timeline)?.label}

Additional Features:
${additionalFeatures.filter(feature => formData[feature.key as keyof QuoteFormData]).map(feature => `- ${feature.label}`).join('\n')}

Estimated Price: $${estimatedPrice.toLocaleString()}

Please contact me to discuss this project in more detail.

Best regards`;

    const mailtoLink = `mailto:contact@avexel.com?subject=${encodeURIComponent(emailSubject)}&body=${encodeURIComponent(emailBody)}`;
    
    // Try to open email client, fallback to contact section
    try {
      window.location.href = mailtoLink;
    } catch (error) {
      // Fallback: scroll to contact section
      const contactSection = document.getElementById('contact');
      if (contactSection) {
        contactSection.scrollIntoView({ behavior: 'smooth' });
      }
    }
    
    console.log('Quote Request:', quoteDetails);
  };

  return (
    <section
      id="quote"
      className="py-32 bg-gradient-to-b from-black to-dark relative overflow-hidden scroll-mt-24"
    >
      {/* Background Effects */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <div className="absolute top-1/4 left-1/4 w-96 h-96 bg-gradient-radial from-primary-500/5 to-transparent opacity-60"></div>
        <div className="absolute bottom-1/4 right-1/4 w-96 h-96 bg-gradient-radial from-secondary-500/5 to-transparent opacity-60"></div>
      </div>

      <div className="container mx-auto px-6 relative z-10">
        <div className="text-center mb-16" data-reveal>
          <h2 className="text-4xl md:text-5xl font-bold mb-6">
            <span className="bg-gradient-to-r from-primary-400 to-secondary-400 bg-clip-text text-transparent">
              Get Your Quote
            </span>
          </h2>
          <p className="text-xl text-gray-400 max-w-3xl mx-auto">
            Tell us about your project and get an instant estimate for your website development needs.
          </p>
        </div>

        <div className="max-w-6xl mx-auto">
          <div className="bg-dark/30 backdrop-blur-sm border border-white/10 rounded-2xl p-6 md:p-8 lg:p-12" data-reveal>
            <div className="grid grid-cols-1 xl:grid-cols-2 gap-8 lg:gap-12">
              {/* Form Section */}
              <div className="space-y-6">
                <h3 className="text-2xl font-semibold text-white mb-6">Project Details</h3>
                
                {/* Website Type */}
                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-2">
                    Website Type *
                  </label>
                  <select
                    value={formData.websiteType}
                    onChange={(e) => handleInputChange('websiteType', e.target.value)}
                    className="w-full px-4 py-3 bg-dark/50 border border-white/10 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500/50 focus:border-primary-500 transition-all duration-300 text-white"
                  >
                    <option value="">Select website type</option>
                    {websiteTypes.map(type => (
                      <option key={type.value} value={type.value}>{type.label}</option>
                    ))}
                  </select>
                </div>

                {/* Number of Pages */}
                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-2">
                    Number of Pages *
                  </label>
                  <select
                    value={formData.numberOfPages}
                    onChange={(e) => handleInputChange('numberOfPages', e.target.value)}
                    className="w-full px-4 py-3 bg-dark/50 border border-white/10 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500/50 focus:border-primary-500 transition-all duration-300 text-white"
                  >
                    <option value="">Select page range</option>
                    {pageRanges.map(range => (
                      <option key={range.value} value={range.value}>{range.label}</option>
                    ))}
                  </select>
                </div>

                {/* Timeline */}
                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-2">
                    Project Timeline *
                  </label>
                  <select
                    value={formData.timeline}
                    onChange={(e) => handleInputChange('timeline', e.target.value)}
                    className="w-full px-4 py-3 bg-dark/50 border border-white/10 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary-500/50 focus:border-primary-500 transition-all duration-300 text-white"
                  >
                    <option value="">Select timeline</option>
                    {timelines.map(timeline => (
                      <option key={timeline.value} value={timeline.value}>{timeline.label}</option>
                    ))}
                  </select>
                </div>

                {/* Additional Features */}
                <div>
                  <label className="block text-sm font-medium text-gray-300 mb-4">
                    Additional Features
                  </label>
                  <div className="grid grid-cols-1 gap-3">
                    {additionalFeatures.map(feature => (
                      <label key={feature.key} className="flex items-center space-x-3 cursor-pointer group">
                        <input
                          type="checkbox"
                          checked={formData[feature.key as keyof QuoteFormData] as boolean}
                          onChange={(e) => handleInputChange(feature.key as keyof QuoteFormData, e.target.checked)}
                          className="w-4 h-4 text-primary-500 bg-dark/50 border-white/20 rounded focus:ring-primary-500 focus:ring-2"
                        />
                        <span className="text-gray-300 group-hover:text-white transition-colors duration-200">
                          {feature.label}
                        </span>
                        <span className="text-sm text-gray-500 ml-auto">
                          +${feature.price.toLocaleString()}
                        </span>
                      </label>
                    ))}
                  </div>
                </div>
              </div>

              {/* Quote Display Section */}
              <div className="lg:pl-8">
                <div className="sticky top-8">
                  <h3 className="text-2xl font-semibold text-white mb-6">Your Quote</h3>
                  
                  {isCalculating ? (
                    <div className="bg-gradient-to-br from-primary-500/10 to-secondary-500/10 border border-primary-500/20 rounded-xl p-6 text-center">
                      <div className="animate-spin w-8 h-8 border-2 border-primary-500 border-t-transparent rounded-full mx-auto mb-4"></div>
                      <p className="text-gray-400">Calculating your quote...</p>
                    </div>
                  ) : showQuote ? (
                    <div className="bg-gradient-to-br from-primary-500/10 to-secondary-500/10 border border-primary-500/20 rounded-xl p-6 space-y-4 animate-fade-in">
                      <div className="text-center">
                        <div className="text-sm text-gray-400 mb-2">Estimated Price</div>
                        <div className="text-4xl font-bold bg-gradient-to-r from-primary-400 to-secondary-400 bg-clip-text text-transparent animate-pulse-price">
                          ${estimatedPrice.toLocaleString()}
                        </div>
                        <div className="text-sm text-gray-500 mt-2">*Starting estimate</div>
                      </div>
                      
                      <div className="border-t border-white/10 pt-4">
                        <h4 className="text-lg font-semibold text-white mb-3">Includes:</h4>
                        <ul className="space-y-2 text-sm text-gray-300">
                          {formData.websiteType && (
                            <li className="flex items-center">
                              <i className="fas fa-check text-primary-400 mr-2"></i>
                              {websiteTypes.find(t => t.value === formData.websiteType)?.label}
                            </li>
                          )}
                          {formData.numberOfPages && (
                            <li className="flex items-center">
                              <i className="fas fa-check text-primary-400 mr-2"></i>
                              {pageRanges.find(p => p.value === formData.numberOfPages)?.label}
                            </li>
                          )}
                          {formData.hasResponsiveDesign && (
                            <li className="flex items-center">
                              <i className="fas fa-check text-primary-400 mr-2"></i>
                              Responsive Design
                            </li>
                          )}
                          {additionalFeatures.map(feature => 
                            formData[feature.key as keyof QuoteFormData] && (
                              <li key={feature.key} className="flex items-center">
                                <i className="fas fa-check text-primary-400 mr-2"></i>
                                {feature.label}
                              </li>
                            )
                          )}
                        </ul>
                      </div>
                      
                      <button
                        onClick={handleGetQuote}
                        className="w-full bg-gradient-to-r from-primary-500 to-secondary-500 text-white font-semibold py-3 px-6 rounded-lg hover:from-primary-600 hover:to-secondary-600 transition-all duration-300 shadow-lg hover:shadow-xl"
                      >
                        Get Detailed Quote
                      </button>
                    </div>
                  ) : (
                    <div className="bg-dark/30 border border-white/10 rounded-xl p-6 text-center">
                      <i className="fas fa-calculator text-4xl text-gray-500 mb-4"></i>
                      <p className="text-gray-400">
                        Select your project details to see an estimated price
                      </p>
                    </div>
                  )}
                  
                  <div className="mt-6 text-xs text-gray-500 text-center">
                    <p>This is an estimate. Final pricing may vary based on specific requirements and complexity.</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default GetQuote;
